import React from 'react'
import Gallery from './../shared/gallery';
import PageTitle from './../shared/page-title';
import SectionTitle from './../shared/section-title';
import Paragraph from './../shared/paragraph';
import PageIntro from '../shared/page-intro';
import Link from './../shared/link';
import Comments from './../shared/comments';

import logo from './../img/wireframe/logo.png';
import img1 from './../img/wireframe/screenshot1.png';
import img2 from './../img/wireframe/screenshot2.png';
import img3 from './../img/wireframe/screenshot3.png';

export default class Page extends React.Component {
    render() {
        return (
            <div>
                <PageTitle text="Wireframe"/>

                <PageIntro logo={logo}>
                    <Paragraph>
                        A collection of shaders and materials for high-quality wireframe rendering in Unity.
                        The package relies on geometry shaders so currently it runs only on Windows
                        and DX10-compatible video cards. Geometry shaders are not yet supported by
                        Unity on Linux and macOS.
                    </Paragraph>
                    <Paragraph>
                        <Link to="https://www.assetstore.unity3d.com/#/content/11638">Get from Unity Asset Store</Link>.
                    </Paragraph>
                </PageIntro>

                <SectionTitle text="Features"/>

                <ul>
                    <li>
                        Six example materials, including single side, two-sided, solid (wireframe + fill color).
                        Wireframe material can be mixed with any other material by simply assigning two (or more)
                        materials to the same object. This requires at least two draw calls though.
                        For simple wireframe rendering with fill color there is a shader included that requires
                        only one draw call.
                    </li>
                    <li>
                        Line width can be changed.
                    </li>
                    <li>
                        Lines are calculated in window space and don’t depend on object’s distance
                        from the camera. So, the farther the object is from the camera, the denser
                        its wireframe. This behavior is the same as for classic wireframe
                        (used, for example, in Unity editor).
                    </li>
                    <li>
                        Lines are antialiased.
                    </li>
                    <li>
                        Deferred and forward rendering support.
                    </li>
                </ul>

                <SectionTitle text="Screenshots"/>
                <Gallery
                    images={[
                        { src: img1 },
                        { src: img2 },
                        { src: img3 }
                    ]}
                />

                <Comments/>
            </div>
        );
    }
}


